body {
  background: linear-gradient(217deg, rgba(224, 247, 218,.8), rgba(224, 247, 218,0) 70.71%),
            linear-gradient(127deg, rgba(237, 234, 209,.8), rgba(237, 234, 209,0) 70.71%),
            linear-gradient(336deg, rgba(234, 215, 247,.8), rgba(234, 215, 247,0) 70.71%);
            background-size: cover;
            background-repeat: no-repeat;
}

.panel {
  padding: 50px 40px;
  margin-bottom: 25px;
}

.project {
  display: flex;
  flex-direction: row;
  padding: 20px 50px;
  margin: 10px 0 25px;
  justify-content: space-between;
  align-items: center;
}

.lagunaImage {
  height: 600px;
  width: auto;
}

.projectcover {
  overflow: hidden;
  border-radius: 10px;
  width: 600px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}

.projectcover:hover {
  transform: scale(1.02);
}

.projectcoverimg {
  width: 100%;
  height: auto;
}

.projectcovercaption {
  width: 40%;
  margin-bottom: 10px;
  margin-left: 25px;
  margin-right: 20px;
}

h1 {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 24px;
}

h2 {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 100px;
  margin-bottom: 10px;
}

h3 {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 24px;
}

.header {
  position: fixed;
  padding: 10px 20px;
  width: 100%;
  background-color: #EAF5E0;
}

.col {
  display: flex;
  flex-direction: column;
}

ul {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 24px;
}

ul li::marker {
  font-size: 12px;
}


.row {
  display: flex;
  flex-direction: row;
}

.col1 {
  width:  40%;
  padding-right: 25px;
  padding-left: 25px;
}

.col2 {
  width: 60%;
}

p {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.centered {
  display: flex;
  justify-content: center;
  width: 100%;
}


.spaced-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.option {
  text-decoration: underline;
  cursor: pointer;
}

.unclicked {
  font-size: 14px;
}



